import VueRouter from 'vue-router';
import Vue from 'vue';
import FormForAddress from '../components/FormForAddress.vue';
import FormMain from '../components/FormMain.vue';
import FormPassport from '../components/FormPassport.vue';
import FormNotFound from '../components/FormNotFound.vue';

Vue.use(VueRouter);

const routes = [
  { name: 'FormMain', component: FormMain, path: '/' },
  { component: FormForAddress, path: '/address' },
  { name: FormPassport, component: FormPassport, path: '/passport' },
  { component: FormNotFound, path: '*' },
];

const router = new VueRouter({
  routes,
});

export default router;
